import animationManager from '../services/animations/animation-manager';
import { SpecPage } from '@stencil/core/testing';

export const playCloseDoubleDrawerAnimation = async (page: SpecPage): Promise<void> => {
  await animationManager.play('closeDoubleDrawer');
  await page.root?.dispatchEvent(new Event('drawerStartsClosing'));
  await page.root?.dispatchEvent(new Event('backdropClosed'));
};
